import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'activitydate'
})
export class ActivitydatePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let newValue : string = '';
    var weekday = new Array(7);
    weekday[0] =  "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";
    //timezone setup
    let d = new Date();
    let localTime = d.getTime();
    let localOffset = d.getTimezoneOffset();
    let utc = localTime + localOffset;;
    let offset = -5;   
    let america = utc + (3600000*offset);
    //given data
    let date  = value;
    let tRemoved = date.replace(/T/, ' ');
    let dotPos = tRemoved.indexOf('.');
    let newDate = tRemoved.substr(0,dotPos);
    //current date
    let date2  = new Date(america).getTime();
    let secDiff = (date2 - new Date(newDate).getTime()) / 1000;
    
    newValue = Math.abs(Math.round(secDiff)) +' seconds ago';
    
    if(secDiff >= 59){
      let minDiff = ((date2 - new Date(newDate).getTime())/1000)/60;
      newValue = Math.abs(Math.round(minDiff)) + ' minutes ago';
      if(minDiff >= 59){
        let hourDiff = ((date2 - new Date(newDate).getTime())/1000)/(60*60);
        newValue = Math.abs(Math.round(hourDiff)) + ' hour ago';
        if(hourDiff >= 23){
          newValue = 'Yesterday';
          if(hourDiff >= 35){
            let day = new Date(newDate).getDay(); 
            newValue = weekday[day];
            if(hourDiff > 144){
              let date = newDate;
              newValue = date;
            }
          }
        }
      }  
    }
    return newValue;
  }

}



