import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  logError : string = '';
  message : string = '';
  resetPassForm : FormGroup;
  
  constructor(private router:Router, private http: HttpClient,fb : FormBuilder) {
    this.resetPassForm = fb.group({
			'email' : ['', Validators.compose([Validators.required, Validators.email])]
		});
  }

  ngOnInit() {
  }

  reset(data,event) : void {
    event.preventDefault();
    const body = 'email=' + data.email;
    this.http.
    post(Constants.API_URL+'profile/user/forget_password', body,{
      headers : new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      .set('Content-Type', 'application/x-www-form-urlencoded')
    }).subscribe((res:any)=>{
      this.message = 'Email sent to ' + data.email;
      setTimeout(() => { this.message = ''; this.router.navigate(['/login']); }, 5000);      
    },(err)=>{
      if(err.error.error === '2001'){
        this.logError = err.error.message;
      }else{
        this.logError = err.message;
      }
      setTimeout(() => { this.logError = ''; }, 5000);
    });
  }

}



