import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Constants } from "./constants";
import 'rxjs/add/operator/map';

@Injectable()
export class FbsyncService {

  constructor(private http:HttpClient) { }
  
  getSyncs(token,chat_id){
    return this.http.get(Constants.API_URL+'address_book/user/'+chat_id+'/device/ALL/syncs',{
            headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)	
          })
          .map((res:any)=> res);
  }

  getContacts(token,chat_id,device){
    return this.http.get(Constants.API_URL + 'address_book/v2/contact_card/' + chat_id + '/device/' + device + "/page/0/limit/20",{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)	
          })
          .map((res:any) => res);
  }

  call3(token,body){
    return this.http.post(Constants.API_URL + 'address_book/v2/contact_card_update', body,{
      headers:new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      .set('X-TWO_DEGREE-APP_TOKEN',token)	
    })
    .map((res:any) => res);
  }

  call4(token,body){
    return this.http.post(Constants.API_URL + 'address_book/v2/contact_card', body,{
      headers : new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      .set('X-TWO_DEGREE-APP_TOKEN',token)	
    })
    .map((res:any) => res);
  }

}



