import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";

@Component({
  selector: 'app-verify-phone',
  templateUrl: './verify-phone.component.html',
  styleUrls: ['./verify-phone.component.css']
})
export class VerifyPhoneComponent implements OnInit {
  phone:string;
  countries:Array<any>;

  phoneVerifyForm : FormGroup;
  show : boolean = true;
  verifyErr : string = '';
  changePhoneErr : string = '';
  resendMsg : string = '';

  constructor(private http:HttpClient,fb : FormBuilder,private router:Router) {
    let country : string = '';
    if(localStorage.getItem('sufData')){
      country =  JSON.parse(localStorage.getItem('sufData')).country_code;
    }else{
      country = '';
    }
      this.phoneVerifyForm = fb.group({
      'code' : ['', Validators.required],
      'country' : [country, Validators.required],
      'phone' : ['', Validators.required]
    })
  }

  ngOnInit() {
    if(localStorage.getItem('surData')){
      this.phone = JSON.parse(localStorage.getItem('surData')).phone_number;
    }else if(localStorage.getItem('newsurData')){
      this.phone = JSON.parse(localStorage.getItem('newsurData')).phone_number;
    }else{
      this.phone = '';
    }
    this.http.get(Constants.API_URL+'config/countries').subscribe(
      (res:any) => {
        this.countries = (res).countries;
      },(err) => {
          console.log(err)
      }
    );
  }

  verifyPhone(data,event){
    event.preventDefault();
    let type:string = 'PHONE_NUMBER';
    let formData:FormData = new FormData();
    formData.append('pin', data.code);
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    this.http
    .post(Constants.API_URL+'profile/user/'+email+'/'+type+'/verify', formData, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        console.log(res);
        localStorage.setItem('resData',JSON.stringify(res));
        let data = {};
        //Sends Email Verification when phone verified
        this.http
        .post(Constants.API_URL+'profile/user/'+email+'/Email/send', data, {
          headers: new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
          })
        .subscribe((res:any) => { console.log(res); });
        this.router.navigate(['/verifyEmail']);
      },(err) => {
        this.verifyErr = err.error.message;
        setTimeout(() => { this.verifyErr = ''; }, 2000);
        console.log(err);
      }
    );
  }

  updateNumber(data,event){
    event.preventDefault();
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    const senddata = {'phone_number': data.phone, 'country_code': data.country};
    this.http
    .put(Constants.API_URL+'profile/user/'+email+'/edit', senddata,{
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        localStorage.setItem('newsurData',JSON.stringify(res));
        this.phone = res.phone_number;
        this.show = true;
        //Send verification code
        this.resendPCode(event);
        console.log(res);
      },(err) => {
        console.log(err);
        if(err.error.detail_description.phone_number)
        this.changePhoneErr = err.error.detail_description.phone_number[0];
        setTimeout(() => { this.changePhoneErr = ''; }, 2000);
      }
    );
  }

  resendPCode(event){
    event.preventDefault();
    let email:string = '';
    let type:string = 'PHONE_NUMBER';
    if(localStorage.getItem('surData')){
      email = JSON.parse(localStorage.getItem('surData')).email;
    }else if(localStorage.getItem('email')){
      email = JSON.parse(localStorage.getItem('email'));
    }
    let data = {};
    this.http
    .post(Constants.API_URL+'profile/user/'+email+'/'+type+'/send', data, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        this.resendMsg = res.message;
        setTimeout(() => { this.resendMsg = ''; }, 2000);
        console.log(res);    
      },(err) => {
        alert(err.error.message);
        console.log(err);
      }
    )
  }
  changePhone(event){
    event.preventDefault();
    this.show = false;
    this.phoneVerifyForm.get('phone').reset();
  }

  cancel($event){
    event.preventDefault();
    this.show = true;
    this.changePhoneErr = '';
  }

}



