import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ProfileService } from '../profile.service';
import * as io from 'socket.io-client';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  @ViewChild('chatBody') private myScrollContainer: ElementRef;
  @ViewChild('msg') message : any;
  @ViewChild('chatMedia') media : ElementRef;
  socket;
  friends : any;
  friendsImg : any;
  token : any;
  email : any;
  loader = false;
  imageBaseUrl : any;
  image : any;
  username : any = 'Chat Bot';
  status : any = '';
  name : any;
  to : any;
  messages = [];
  chat_id : any;
  friendData : any;
  chatData = [];
  lastMessage = [];
  usersStatus = [];
  mediaFileUrl : any;
  imgUploadLoader = false;

  constructor(private profile:ProfileService) {
    this.chat_id = JSON.parse(localStorage.getItem('resData')).chat_id;
    this.token = localStorage.getItem('token');
    this.name = JSON.parse(localStorage.getItem('resData')).name;
    this.image = JSON.parse(localStorage.getItem('resData')).profile_image;
    
    //this.socket = io();
    this.socket = io.connect('https://app.twodegrees.io:4200');
    
    this.socket.emit('user_online',this.chat_id,this.name);

    this.socket.on('users-status',(data) => {
      this.usersStatus = [];
      if(data.length>0){
        for(let i=0;i<data.length;i++){
          if(data[i].chat_id != this.chat_id){
            this.usersStatus.push(data[i])
          }
        }
      }
      this.status = 'online';
    })
    console.log(this.usersStatus)

    this.socket.on('new-message',(data) => {
      this.chatData.push(data);
      console.log(data);
    })

    this.token  = localStorage.getItem('token');
    this.email = JSON.parse(localStorage.getItem('resData')).email;
    this.imageBaseUrl = localStorage.getItem('imgBaseUrl');
    
    this.loader = true;
    this.profile.chatList(this.token,this.chat_id)
    .subscribe((res:any) => {
      console.log(res);
      this.friends = res.chats;
      this.loader = false;
    },err => { console.log(err)});
  }

  ngOnInit() {
  }

  chatWith(user){
    console.log(user)
    this.username = user.name;
    this.to = user.chat_id;
    this.friendsImg = user.profile_image;
    this.status = 'offline';
    this.friendData = user;
    //shows current online user's status
    if(this.usersStatus.length > 0){
      for(let i=0; i<this.usersStatus.length; i++){
        if(this.usersStatus[i].chat_id == user.chat_id){
          this.status = 'online';
          break;
        }
      }
    }
    //get chat history
    this.profile.chatHistory(this.token,this.chat_id,user.chat_id)
    .subscribe((res:any) => { 
      console.log(JSON.parse(res.chats[0].body));
      console.log(res);
      const data = { chatid : user.chat_id , lastMsg : JSON.parse(res.chats[0].body).text }
      this.lastMessage.push(data);
      console.log(this.lastMessage)
      this.chatData = [];
      for(let i=(res.chats.length-1); i >= 0; i--){
        let converted_data = JSON.parse(res.chats[i].body)
        this.chatData.push(converted_data)
      }
      console.log(this.chatData);
    },err => {console.log(err)});

  }
  
  sendMessage(msg){
    if(msg.trim().length > 0){
      const data = {
                  message : msg,
                  rid : this.friendData.chat_id,
                  rname : this.friendData.name,
                  sid : this.chat_id,
                  sname : this.name,
                  image_url : this.mediaFileUrl,
                  msgts : Date.parse(Date()),
                  type: 0,
                  mediaURL: this.friendData.profile_image
      }
      this.socket.emit('send-message',data); 
      this.message.nativeElement.value = '';
    }
  }

  uploadImage(){
    this.imgUploadLoader = true;
    let file = this.media.nativeElement.files[0];
    let image:FormData = new FormData();
        image.append('file',file)
    this.profile.uploadChatImage(this.token,image)
    .subscribe((res:any)=>{
      this.imgUploadLoader = false;
      this.media.nativeElement.value = '';
      this.sendImageToXmppServer(res.full_path); 
    },err=>{console.log(err); this.imgUploadLoader = false; alert('No image Uploaded![png,jpg allowed only]')});
  }

  sendImageToXmppServer(chatImage){
    const data = {
          message : '',
          rid : this.friendData.chat_id,
          rname : this.friendData.name,
          sid : this.chat_id,
          sname : this.name,
          image_url : this.friendData.profile_image,
          msgts : Date.parse(Date()),
          type: 1,
          mediaURL: chatImage
    }
    this.socket.emit('send-message',data); 
  }

}



