import { Component, OnInit, ContentChild, ViewChild,ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { ProfileService } from '../profile.service'
import { Router } from '@angular/router';
import { ModalDirective } from 'ngx-bootstrap/modal/modal.directive';
import { PhonePipe } from '../phone.pipe';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css'],
})
export class SettingComponent implements OnInit {
  @ViewChild('verification') verifyModal:ModalDirective;
  @ViewChild('pin') pin:any;
  @ViewChild('pin2') pin2:any;

  emailError : string = ''; verifyError: string = ''; phoneError : string = ''; ccode : string = ''; phone : number;
  loader :boolean = false; location;
  emailVerified : boolean = false; verifyEmail : boolean = false; phoneVerified : boolean = false; verifyPhone : boolean = false;
  settingForm : FormGroup;
  private email; private token;
  countries : Array<any>;
  newData :any = {};
  
  constructor(fb : FormBuilder,private profile:ProfileService,private router:Router) {
    let data = JSON.parse(localStorage.getItem('resData'));
    let firstName,lastName,password;
    this.token = localStorage.getItem('token');
    if(this.token){
      let new_email = data.email;
      firstName = data.name.split(' ').slice(0, -1).join(' ');
      lastName = data.name.split(' ').slice(-1).join(' ');
      password = "123456";
      this.emailVerified = data.email_verified;
      this.phoneVerified = data.phone_number_verfied;
      this.email = data.email;
      this.location = data.location;
      if(data.new_email) new_email = data.new_email;
      if(data.new_phone_number) {
        this.phone = this.getPhoneNumber(data.new_phone_number);
      }else{this.phone = this.getPhoneNumber(data.phone_number);}
      this.settingForm = fb.group({
        'fname' : [firstName, Validators.required],
        'lname' : [lastName,Validators.required],
        'email' : [new_email, Validators.compose([Validators.required, Validators.email])],
        'password' : [password, Validators.compose([Validators.required])],
        'ccode' : [this.ccode,Validators.required],
        'phone' : [this.phone, Validators.required],
        'gender' : [data.gender,Validators.required]
      });
    }else{
      this.router.navigate(['/login']);
    }
    this.settingForm.valueChanges.subscribe(
      (res) => {
        let name,phone,resPhone;
            phone = res.phone; phone = phone.replace(/\s/g, ''); phone = phone.replace(/[()]/g, '');
            resPhone = res.phone; resPhone = resPhone.replace(/\s/g, ''); resPhone = resPhone.replace(/[()]/g, '');
        if(res.fname !== firstName || res.lname !== lastName){ name=res.fname+' '+res.lname; this.newData.name=name;}else{ delete this.newData.name; }
        if(res.gender !== data.gender) {this.newData.gender = res.gender;}else{delete this.newData.gender;}
        if(res.email !== data.email) {this.newData.email = res.email;}else{delete this.newData.email;}
        if(res.password !== password) {this.newData.password = res.password;}else{delete this.newData.password;}
        if(resPhone !== this.phone) {this.newData.phone_number = phone;this.newData.country_code = res.ccode;}else{delete this.newData.phone_number;delete this.newData.country_code;}
        console.log(this.newData)
      }
    );
  }

  ngOnInit() {
    let data = JSON.parse(localStorage.getItem('resData'));
    let countries = JSON.parse(localStorage.getItem('countries')); 
    if(countries !== null){
      this.countries = countries;
      this.ccode = this.getCountryCode(data.phone_number,this.countries);
      this.settingForm.patchValue({ccode:this.ccode})
    }else{
      this.profile.countries().subscribe((res:any)=>{
        localStorage.setItem('countries',JSON.stringify(res.countries)); 
        this.countries = res.countries;
        this.ccode = this.getCountryCode(data.phone_number,this.countries);
        this.settingForm.patchValue({ccode:this.ccode})
      });
    }
  }

  updateLocation(){
    let body = this.location == true ? { unblock_time : '1' } : { unblock_time : '86400' };
    let loc = this.location == true ? 'UN_BLOCK' : 'BLOCK';
    this.profile.shareLocation(this.email,this.token,body,loc)
    .subscribe((res:any) => {console.log(res)
      let data = JSON.parse(localStorage.getItem('resData'));
      data.location = this.location;
      data.score = this.location ? data.score + 15 : data.score - 15;
      localStorage.setItem('resData',JSON.stringify(data));
      alert('Location Updated');
    },err => {console.log(err)
      alert('Location Update Fails');
    });
  }

  getPhoneNumber(data):number{
    let pn = data;
    let pnc = pn.substr(pn.length - 10);
    return pnc;
  }

  getCountryCode(data,countries):string{
    let pn = data;
    let pnc = pn.substr(pn.length - 10);
    let cc = pn.substr(0,pn.length-pnc.length);
    for(let i=0;i<countries.length;i++){
      if(cc === countries[i].calling_code){
        cc = countries[i].alpha_code;
      }
    }
    return cc;
  }

  update(data){
    let type;
    if(this.newData.email){
      this.profile.updateProfile(this.email,this.token,this.newData)
      .subscribe((res:any)=>{console.log(res);
        localStorage.setItem('resData',JSON.stringify(res));
        type = 'EMAIL';
        this.profile.sendCode(this.email,type)
        .subscribe((res:any)=>{console.log(res);
          this.emailError = 'Verification Code Sent'; setTimeout(() => { this.emailError = ''; }, 3000);
          this.emailVerified = false;
          this.verifyEmail = false;
        },err=>{console.log(err)})
      },err=>{console.log(err);
        if(err.error.detail_description.email[0]) this.emailError = 'Email Already Exist'; setTimeout(() => { this.emailError = ''; }, 3000); this.settingForm.patchValue({email:this.email});
      });
    }else if(this.newData.phone_number){
      this.profile.updateProfile(this.email,this.token,this.newData)
      .subscribe((res:any)=>{console.log(res);
        localStorage.setItem('resData',JSON.stringify(res));
        type = 'PHONE_NUMBER';
        this.profile.sendCode(this.email,type)
        .subscribe((res:any)=>{console.log(res);
          this.phoneError = 'Verification Code Sent'; setTimeout(() => { this.phoneError = ''; }, 3000);
          this.phoneVerified = false;
          this.verifyPhone = false;
        },err=>{console.log(err)})
      },err=>{console.log(err);
        if(err.error.detail_description.phone_number) this.phoneError = err.error.detail_description.phone_number[0]; setTimeout(() => { this.phoneError = ''; }, 3000);
      });
    }else{
      if(Object.keys(this.newData).length){
        this.profile.updateProfile(this.email,this.token,this.newData)
        .subscribe((res:any)=>{console.log(res);
          localStorage.setItem('resData',JSON.stringify(res));
          alert('Data updated');
        },err=>{console.log(err)});
      }else{ console.log('Nothing is updated!'); }
    }
    /*console.log(data);let name = data.fname + ' ' +data.lname;delete data.fname;delete data.lname;data.name = name;*/
  }

  setVerifyModal(type){
    if(type == 'email'){
      this.verifyEmail = true;
      this.verifyModal.show();
    }else{
      this.verifyPhone = true;
      this.verifyModal.show();
    }
  }

  resendCode(type){
    this.profile.sendCode(this.email,type)
    .subscribe((res:any)=>{console.log(res);
      alert('Code Sent');
    },err=>{console.log(err)})
  }

  verify(){
    let type;
    if(this.pin){
      this.loader = true;
      type = 'EMAIL';
      this.profile.verify(this.email,type,this.pin.nativeElement.value)
      .subscribe((res:any)=>{console.log(res);
        this.loader = true;
        this.emailVerified = true;
        this.verifyModal.hide();
        localStorage.setItem('resData',JSON.stringify(res.user));
      },err=>{ console.log(err);
        this.loader = false;
        if(err.error.message) this.verifyError = 'Invalid Pin'; setTimeout(() => { this.verifyError = ''; }, 3000);
      });
    }else{
      this.loader = true;
      type = "PHONE_NUMBER"
      this.profile.verify(this.email,type,this.pin2.nativeElement.value)
      .subscribe((res:any)=>{console.log(res);
        this.loader = true;
        this.phoneVerified = true;
        this.verifyModal.hide();
        localStorage.setItem('resData',JSON.stringify(res.user));
      },err=>{ console.log(err);
        this.loader = false;
        if(err.error.message) this.verifyError = 'Invalid Pin'; setTimeout(() => { this.verifyError = ''; }, 3000);
      });
    }
  }

}



