import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule,Routes,CanActivate } from '@angular/router';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { ModalModule } from 'ngx-bootstrap/modal';
import { Ng4GeoautocompleteModule } from 'ng4-geoautocomplete';
import { DndModule } from 'ng2-dnd';
import { AgmCoreModule,GoogleMapsAPIWrapper } from '@agm/core';
import { AgmDirectionModule } from 'agm-direction';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './auth.guard';
import { RegisterationComponent } from './registeration/registeration.component';
import { EditRegistrationComponent } from './edit-registration/edit-registration.component';
import { VerifyPhoneComponent } from './verify-phone/verify-phone.component';
import { VerifyEmailComponent } from './verify-email/verify-email.component';
import { FbSignupComponent } from './fb-signup/fb-signup.component';
import { FbSyncComponent } from './fb-sync/fb-sync.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ImageCropperComponent } from 'ng2-img-cropper';
import { ProfileComponent } from './profile/profile.component';
import { SettingComponent } from './setting/setting.component';
import { PhonePipe } from './phone.pipe';
import { PhoneDirective } from './phone.directive';
import { DiscoverComponent } from './discover/discover.component';

import { ProfileService } from './profile.service';
import { DiscoverService } from './discover.service';
import { FbsyncService } from './fbsync.service';
import { ActivitydatePipe } from './activitydate.pipe';
import { HangoutComponent } from './hangout/hangout.component';
import { DistancePipe } from './distance.pipe';
import { HangoutTimePipe } from './hangout-time.pipe';
import { EditHangoutComponent } from './edit-hangout/edit-hangout.component';
import { MutualFriendsPipe } from './mutual-friends.pipe';
import { ChatComponent } from './chat/chat.component';
import { Ng2EmojiModule } from 'ng2-emoji';

const appRoutes: Routes = [
	{ path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: LoginComponent },
  { path: 'forgot', component: ForgotPasswordComponent },
  { path: 'signup', component: RegisterationComponent },
  { path: 'fbSignup', component: FbSignupComponent },
  { path: 'editSignup', component: EditRegistrationComponent },
  { path: 'verifyPhone', component: VerifyPhoneComponent },
  { path: 'verifyEmail', component: VerifyEmailComponent },
  { path: 'fbSync', component: FbSyncComponent },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'discover', component: DiscoverComponent },
  { path: 'chat', component: ChatComponent },
  { path: 'hangout', component: HangoutComponent },
  { path: 'editHangout', component: EditHangoutComponent },
  { path: 'account', component: SettingComponent },
  { path: 'profile', component: ProfileComponent },
	{ path: '**', redirectTo: 'login', pathMatch: 'full'}
];

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    LoginComponent,
    RegisterationComponent,
    EditRegistrationComponent,
    VerifyPhoneComponent,
    VerifyEmailComponent,
    FbSignupComponent,
    FbSyncComponent,
    ForgotPasswordComponent,
    ImageCropperComponent,
    ProfileComponent,
    SettingComponent,
    PhonePipe,
    PhoneDirective,
    DiscoverComponent,
    ActivitydatePipe,
    HangoutComponent,
    DistancePipe,
    HangoutTimePipe,
    EditHangoutComponent,
    MutualFriendsPipe,
    ChatComponent
  ],
  imports: [
    HttpClientModule,
    FormsModule,
    BrowserModule,
    ReactiveFormsModule,
    RouterModule.forRoot(appRoutes),
    ModalModule.forRoot(),
    Ng4GeoautocompleteModule.forRoot(),
    DndModule.forRoot(),
    AgmCoreModule.forRoot({
      apiKey : 'AIzaSyB8UgJ8dYPHFXtkDsfoh_E3kyONeUDYe5w',
      libraries : ['places']
    }),
    AgmDirectionModule,
    Ng2EmojiModule.forRoot()
  ],
  providers: [GoogleMapsAPIWrapper,FbsyncService,ProfileService,DiscoverService,{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }



