import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";

@Component({
  selector: 'app-edit-registration',
  templateUrl: './edit-registration.component.html',
  styleUrls: ['./edit-registration.component.css']
})

export class EditRegistrationComponent implements OnInit {
  countries : Array<any>;
  sufData : Array<any>;
  surData : Array<any>;

  editSignupForm : FormGroup;
  editbtn: boolean = false;
  updatemsg: string = '';
  _formErr:string = '';
  
  constructor(private router:Router, private http: HttpClient,fb : FormBuilder) {
    let data = JSON.parse(localStorage.getItem('sufData'));
    this.editSignupForm = fb.group({
      'name' : [{value : data.name, disabled : true}, Validators.compose([Validators.required])],
			'email' : [{value : data.email, disabled : true}, Validators.compose([Validators.required, Validators.email])],
      'password' : [{value : data.password, disabled : true}, Validators.compose([Validators.required])],
      'country' : [{value : data.country_code, disabled : true}, Validators.required],
      'phone' : [{value : data.phone_number, disabled : true}, Validators.compose([Validators.required])],
      'gender' : [{value : data.gender, disabled : true}, Validators.required]
    })
  }

  ngOnInit() {
    this.surData = JSON.parse(localStorage.getItem('surData'));
    this.sufData = JSON.parse(localStorage.getItem('sufData'));
    //console.log(this.surData.access_token,this.surData.email);
     //fetch countries data
     this.http.get(Constants.API_URL+'config/countries').subscribe(
      (res:any) => {
        this.countries = (res).countries;
      },(err) => {
          console.log(err)
      }
    );
  }

  update(data,event){
    event.preventDefault();
    const dataArray = {"name": data.name, "gender": data.gender, "phone_number": data.phone, "password": data.password, "country_code": data.country };
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    let accessToken:string = JSON.parse(localStorage.getItem('surData')).access_token;
    this.http
    .put(Constants.API_URL+'profile/user/'+email, dataArray, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      .set('X-TWO_DEGREE-APP_TOKEN',accessToken),
      })
    .subscribe(
      (res:any) => {
        console.log(email,accessToken);
        localStorage.setItem('sufData',JSON.stringify(dataArray));
        localStorage.setItem('newsurData',JSON.stringify(res));
        this.updatemsg = 'Information update Successfully!'; 
        this.close(event);
      },(err) => {
        console.log(err)
    });
  }

  verifyPhone(event){
    event.preventDefault();
    let type:string = 'PHONE_NUMBER';
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    let data = {};
    this.http
    .post(Constants.API_URL+'profile/user/'+email+'/'+type+'/send', data, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        localStorage.setItem('steps', 'phoneverify');
        localStorage.setItem('pvMsg',res.message);
        this.router.navigate(['/verifyPhone']);    
      },(err) => {
        this._formErr = err.message;
        console.log(err);
      }
    )
  }

  edit(event){
    event.preventDefault();
    this.editbtn = true;
    this.editSignupForm.get('name').enable();
    this.editSignupForm.get('email').enable();
    this.editSignupForm.get('password').enable();
    this.editSignupForm.get('phone').enable();
    this.editSignupForm.get('country').enable();
    this.editSignupForm.get('gender').enable();
  }

  close(event){
    event.preventDefault();
    this.editbtn = false;
    this.editSignupForm.get('name').disable();
    this.editSignupForm.get('email').disable();
    this.editSignupForm.get('password').disable();
    this.editSignupForm.get('phone').disable();
    this.editSignupForm.get('country').disable();
    this.editSignupForm.get('gender').disable();
  }

}



