import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";
import { DomSanitizer } from '@angular/platform-browser';

declare var window: any;
declare var FB: any;

@Component({
  selector: 'app-registeration',
  templateUrl: './registeration.component.html',
  styleUrls: ['./registeration.component.css']
})
export class RegisterationComponent implements OnInit {
  countries : Array<any>;
  signupForm : FormGroup;
  
  formInvalid:boolean = false;
  emailErr: string = '';
  phoneErr: string = '';
  rPassErr: string = '';
  agreeErr: string = '';
  _formErr: string = '';

  constructor(private _sanitizer: DomSanitizer,private router:Router, private http: HttpClient,fb : FormBuilder) {
    this.signupForm = fb.group({
      'name' : ['', Validators.compose([Validators.required,Validators.minLength(2)])],
			'email' : ['', Validators.compose([Validators.required, Validators.email])],
      'password' : ['', Validators.compose([Validators.required, Validators.minLength(6)])],
      'rpassword' : ['', Validators.compose([Validators.required, Validators.minLength(6)])],
      'country' : ['', Validators.required],
      'phone' : ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(12)])],
      'gender' : ['', Validators.required],
      'agree' : ['', Validators.required]
    })
    
    this.signupForm.valueChanges.subscribe(
      (data) => {
        if(data.password && data.rpassword)
        this.rPassErr = data.password === data.rpassword ? '' : 'Password Mismatch!';
        this.phoneErr = '';
        this.emailErr = '';
        this._formErr = '';
        this.agreeErr = '';
      }
    );

    (function(d, s, id){
			var js, fjs = d.getElementsByTagName(s)[0];
			if (d.getElementById(id)) {return;}
			js = d.createElement(s); js.id = id;
			js.src = '//connect.facebook.net/en_US/sdk.js';
			fjs.parentNode.insertBefore(js, fjs);
		}(document, 'script', 'facebook-jssdk'));
		window.fbAsyncInit = () => {
			console.log("fbasyncinit")
			FB.init({
				appId            : '620810454655743',
				autoLogAppEvents : true,
				status			 : true,
				cookie			 : true,
				xfbml            : true,
				version          : 'v2.6'
			});
			FB.AppEvents.logPageView();
		};
   }

  ngOnInit() {
    //fetch countries data
    this.http.get(Constants.API_URL+'config/countries').subscribe(
      (res:any) => {
        this.countries = (res).countries;
        //console.log(data)
      },(err) => {
          console.log(err)
      }
    );
  }

  submit(data) {
    const dataArray = {"name": data.name, "gender": data.gender, "phone_number": data.phone, "email": data.email, "password": data.password, "country_code": data.country, "device_token": '192.169.1.1', 'device_type': 'WEB', "profile_type": 'EMAIL', "profile_id": Math.random()};
    if(data.agree){
      this.formInvalid = true;
    }else{
      this.agreeErr = 'Select agree checkbox to Continue!'
    }
    if(this.formInvalid){
      this.http
      .post(Constants.API_URL+'profile/user', dataArray, {
        headers: new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID),
        })
      .subscribe(
        (res:any) => {
          localStorage.setItem('sufData',JSON.stringify(dataArray));
          localStorage.setItem('surData',JSON.stringify(res)); 
          this.router.navigate(['/editSignup']); 
        },(err) => { 
          this.formInvalid = false;
          this._formErr = err.error.message;
          if(err.error.detail_description.email){
            this.emailErr = err.error.detail_description.email[0];
          }
          if(err.error.detail_description.phone_number[0] === 'This phone number is already in use'){
            this.phoneErr = err.error.detail_description.phone_number[0];
          }else if(err.error.detail_description.phone_number){
            this.phoneErr = err.error.detail_description.phone_number[1];
          }
      });
    } 
  }

  regWithFb(event){
    event.preventDefault();
		FB.getLoginStatus((response) => {
			if (response.status === 'connected') {
				let accessToken : string = response.authResponse.accessToken;
				this.signupAPI(accessToken);
			}else{
				this.initFBLogin(event);
			}
		})
  }

  initFBLogin(event){
		FB.login((response)=>{
			this.regWithFb(event);
		},{scope:'email,public_profile'});
  }
  
  signupAPI(token){
    let url : string = '/me?fields=name,email,about,gender';
    FB.api(url, (response) => {
			console.log(response)
			let email : string = response.email,
          user_id : string = response.id,
          user_name : string = response.name;
      let gender : string = response.gender.toUpperCase();
			this.http.get(Constants.API_URL+'profile/check_social_login/FACE_BOOK/'+user_id,{
				headers:new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)	
			})
			.subscribe((res:any) => {
				const body = {account_id: user_id, password : token, login_type : 'FACE_BOOK'};
				this.http
				.post(Constants.API_URL+'profile/login', body, {
				  headers: new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID),
					})
				.subscribe((res:any) => {
					this.outFromFb();
					localStorage.setItem("actoken", res.access_token);
					localStorage.setItem("object", JSON.stringify(res));
					localStorage.setItem("token", res.access_token);
					this.router.navigate(['/dashboard']);
				},(err) => {
					console.log(err)
				})
			},(err) => {
        console.log(err.error.error);
        this.outFromFb();
        if(err.error.error === '2001'){
          var body = {"name": user_name, "gender": gender, "email": email, "country_code": 'US', "device_token": '192.168.1.1', 'device_type': 'WEB', 'profile_id': user_id, 'profile_token': token, "profile_type": 'FACE_BOOK'};
          this.http
          .post(Constants.API_URL+'profile/user', body, {
            headers: new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID),
            })
          .subscribe((res:any) => {
            localStorage.setItem('token', res.access_token);
            localStorage.setItem('object', JSON.stringify(res));
            localStorage.setItem('sufData',JSON.stringify(body));
            this.outFromFb();
            this.router.navigate(['/fbSignup']);
          },(err) => {
            this._formErr = err.message;
            if(err.detail_description.email[0]){
              this._formErr = err.detail_description.email[0];
            }else if(err.error.error === '1202'){
              this._formErr = err.message;
              this.outFromFb();
            }else{
              this._formErr = err.message;
              this.outFromFb();
            }
          })
        }
			})
		})
  }

  outFromFb(){
		FB.getLoginStatus((response:any) => {
			if (response && response.status === 'connected') {
				FB.logout((response:any) => {

				});
			}
		});
	}

}



