import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ProfileService } from '../profile.service';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  @ViewChild('mutualFrndzModal') mfModal: ModalDirective;

  private email; private token; private imgBaseUrl;

  loader : boolean = false;
  image : string = '';
  name : string = '---';
  age : number = 0;
  gender : string = '---';
  city : string = '---';
  state : string = '---';
  mutualFrndz : number = 0;
  hometown : string = 'Not Set';
  userData : Array<any>;
  friends : Array<any>;
  mutualFriends : Array<any>;
  orgData : Array<any>;
  serOrgData : Array<any>; nSerOrgData : string;
  serEduData : Array<any>; nSerEduData : string;
  education = [];

  constructor(private router:Router,private profile:ProfileService) { 
    this.token = localStorage.getItem('token');
  	if(!this.token){
  		router.navigate(['login']);
    }else{
      let storedFrndzData = JSON.parse(localStorage.getItem('friends'));
      let userEmail = localStorage.getItem('frndEmail');
      for(let i=0;i<storedFrndzData.friends.length;i++){
        if(storedFrndzData.friends[i].email === userEmail){
          this.userData = storedFrndzData.friends[i];
        }
      }
      let storeImgBaseUrl = localStorage.getItem('imgBaseUrl');
      if(storeImgBaseUrl !== null){
        this.imgBaseUrl = storeImgBaseUrl;
        this.setImageFromStoredData(this.imgBaseUrl,this.userData);
      }
      this.viewProfile(localStorage.getItem('frndEmail'));
    }
  }

  ngOnInit() {
    this.setView(this.userData);
    let degree = 'ZERO_DEGREE',
    sortBy = 'name__ASC',
    page = 0;
    this.profile.friendsList(this.email,this.token,degree,sortBy,page)
    .subscribe((res:any)=>{
      this.friends = res.friends;
      this.mutualFrndz = this.totalMutualFrndz(res.friends);
    },err=>{console.log(err)});
  }

  totalMutualFrndz(data):number{
    let count = 0;
    for(let i=0;i<data.length;i++){
      count += data[i].common_friends_count;
    }
    return count;
  }

  favorite(){
    let myemail = JSON.parse(localStorage.getItem('resData')).email;
    this.profile.favorite(this.token,myemail,this.email)
    .subscribe((res:any) => {
      alert('Updated');
    },err => { console.log(err); });
  }

  viewProfile(userEmail){
    let degree = 'ZERO_DEGREE',
    sortBy = 'name__ASC',
    page = 0;
    this.profile.getProfile(userEmail,this.token)
    .subscribe((res:any)=>{
      console.log(res);
      localStorage.setItem('profile',JSON.stringify(res));
      this.setView(res);
      this.setImage(localStorage.getItem('imgBaseUrl'));
      this.profile.friendsList(res.email,this.token,degree,sortBy,page)
      .subscribe((res:any)=>{
        this.friends = res.friends;
        this.mutualFrndz = this.totalMutualFrndz(res.friends);
      },err=>{console.log(err)});
    },(err) => {
      console.log(err);
    });
  }

  viewMutualFriends(userEmail){
    this.mutualFriends = [];
    this.mfModal.show();
    this.loader = true;
    this.profile.getMutualFriends(this.email,this.token,userEmail)
    .subscribe((res:any)=>{
      this.loader = false;
      console.log(res); this.mutualFriends = res.mutual_friends;
    },err=>{ this.loader = false; console.log(err)});
  }

  setImage(baseUrl){
    let img = JSON.parse(localStorage.getItem('profile')).profile_image;
    if(img !== undefined && baseUrl !== null){
      this.image = baseUrl + img;
    }else{
      this.image = '';
    }
  }

  setImageFromStoredData(baseUrl,data){
    let img = data.profile_image;
    if(img !== undefined && baseUrl !== null){
      this.image = baseUrl + img;
    }else{
      this.image = '';
    }
  }

  setView(data){
    this.email = data.email;
    if(data.dob){
    let age1 = data.dob;
    if(age1) {let newAge = age1.substr(0,age1.indexOf('T'));this.age = this.getAge(newAge);}
    }
    this.name = data.name;
    this.gender = data.gender;
    if(data.city){
      if(Object.keys(data.current_city).length) {this.city = data.current_city.name;this.state = data.current_city.state;}
    }
    if(data.current_city){
      if(Object.keys(data.city).length) {this.hometown = data.city.name +', '+ data.city.state;}
    }
    if(data.gos){
      if(Object.keys(data.gos).length) {this.serOrgData = data.gos;}else{this.nSerOrgData = 'Not Set';}
    }
    if(data.education){
      if(Object.keys(data.education).length) {this.serEduData = data.education;}else{this.nSerEduData = 'Not Set';}
    }
  }

  getAge(birthday):number{
    let dob = new Date(birthday); 
    var ageDifMs = Date.now() - dob.getTime();
    var ageDate = new Date(ageDifMs);
    let age = Math.abs(ageDate.getUTCFullYear() - 1970);
    return age;
  }

  hideMfModal(): void {
    this.mfModal.hide();
  }

}



