import { Component, OnInit, ElementRef, ViewChild ,NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { Constants } from "../constants";
import { ModalDirective } from 'ngx-bootstrap/modal';
import { ImageCropperComponent, CropperSettings, Bounds} from 'ng2-img-cropper';
import { ProfileService } from '../profile.service';
import { FormControl } from '@angular/forms';
import { } from 'googlemaps';
import { MapsAPILoader } from '@agm/core';
import { ActivitydatePipe } from '../activitydate.pipe';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  @ViewChild("search") public searchElementRef: ElementRef;
  @ViewChild("city") public cityElement: ElementRef;
  @ViewChild("school") public schoolElement: ElementRef;
  @ViewChild('cropper') cropper:ImageCropperComponent;
  @ViewChild('childModal') childModal: ModalDirective;
  @ViewChild('mutualFrndzModal') mfModal: ModalDirective;
  @ViewChild('profileScoreModal') psModal: ModalDirective;
  @ViewChild('updateModal') updateModal: ModalDirective;
  @ViewChild('file') fileInput:any;
  @ViewChild('file2') fileInput2:any;
  @ViewChild('dob') dob:any;
  @ViewChild('org') cat:any;
  @ViewChild('searchStr') searchStr:any;
  @ViewChild('orgId') orgId:any; 
  @ViewChild('edu') eduType:any;

  public searchControl: FormControl; public citySearch: FormControl; public schoolSearch: FormControl;
  private email; private token; public imgBaseUrl; private imgType;
  private hLat; private hLng; private cLat; private cLng;
  private eLat; private eLng; private eAddress; private ePlaceId; private ePlaceName; private ePlaceType;
  
  edit : boolean = false; loader : boolean = false; sync:boolean; shareLoc:boolean; pVerify:boolean; eVerify:boolean;
  image : string = ''; editHomeTown : boolean = false; editCity : boolean = false; editOrg : boolean = false;
  fImage : string = ''; editEdu : boolean = false; editDp : boolean = false; editFImg : boolean = false;
  name : string = '---';
  age : number = 0; age1:string;
  gender : string = '---';
  city : string = '---';
  state : string = '---';
  imgUploading :string = '';
  score : string = '';
  mutualFrndz : number = 0;
  hometown : string = 'Not Set';
  userData : Array<any>;
  friends : Array<any>;
  mutualFriends : Array<any>;
  checkInData : Array<any>; 
  orgData : Array<any>;
  featuredImages : Array<any>;
  serOrgData : Array<any>; nSerOrgData : string;
  serEduData : Array<any>; nSerEduData : string;
  education = [];
  data:any; relationship:any;
  cropperSettings: CropperSettings;

  constructor(private router:Router,private profile:ProfileService,private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone) {
  	this.token = localStorage.getItem('token');
  	if(!this.token){
  		router.navigate(['login']);
    }else{
        let storeImgBaseUrl = localStorage.getItem('imgBaseUrl');
        if(storeImgBaseUrl){
            this.imgBaseUrl = storeImgBaseUrl;
            this.setImages(this.imgBaseUrl);
        }else{
            this.profile.config()
            .subscribe((res:any) => {
              localStorage.setItem('imgBaseUrl',res.CONFIG.IMAGE_BASE_URL);
              this.imgBaseUrl = res.CONFIG.IMAGE_BASE_URL;
              this.setImages(this.imgBaseUrl);    
            },(err) => {console.log(err)});
        }
    }
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.noFileInput = true;
    this.cropperSettings.preserveSize = true;
    this.cropperSettings.keepAspect = false;
    this.cropperSettings.canvasWidth = 500;
    this.cropperSettings.canvasHeight = 400;
    this.data = {}; 
  }

  ngOnInit() {
    //return Math.abs(Math.round(diff));
    this.searchControl = new FormControl();
    this.citySearch = new FormControl ();
    this.schoolSearch = new FormControl ();
    let userData = JSON.parse(localStorage.getItem('resData'));
    let chatId = userData.chat_id;
    console.log(userData)
    this.setView(userData);
    let degree = 'ZERO_DEGREE',
        sortBy = 'name__ASC',
        page = 0;
    let friends = JSON.parse(localStorage.getItem('friends'));
    if(friends != null){
      this.friends = friends.friends;
      this.mutualFrndz = this.totalMutualFrndz(friends.friends);
    }else{
      //get Friend List
      this.profile.friendsList(this.email,this.token,degree,sortBy,page)
      .subscribe((res:any)=>{console.log(res);
        localStorage.setItem('friends',JSON.stringify(res));
        this.friends = res.friends;
        this.mutualFrndz = this.totalMutualFrndz(res.friends);
      },err=>{console.log(err)});
    }
    //get all Checkin 
    this.profile.getCheckIn(this.email,this.token)
    .subscribe((res:any) => { console.log(res.hangouts)
      this.checkInData = res.hangouts;
    },err => { console.log(err)});
  }

  totalMutualFrndz(data):number{
    let count = 0;
    for(let i=0;i<data.length;i++){
      count += data[i].common_friends_count;
    }
    return count;
  }

  searchOrg(){
    let cat = this.cat.nativeElement.value, searchStr = this.searchStr.nativeElement.value; 
    this.profile.searchOrg(this.token,cat,searchStr)
    .subscribe((res:any)=>{
      console.log(res);
      this.orgData = res;
    },err=>{
      console.log(err)
      alert('Nothing Found!');
    });
  }

  delOrg(id){
    this.profile.delOrg(this.token,this.email,id)
    .subscribe((res:any)=>{
      console.log(res)
      this.profile.getProfile(this.email,this.token)
      .subscribe((res:any)=>{
        localStorage.setItem('resData',JSON.stringify(res));
        this.setView(res);
      },(err)=>{console.log(err)});
    },(err)=>{console.log(err)});
  }

  delActivity(id){
    this.loader = true;
    this.profile.delCheckIn(this.email,this.token,id)
    .subscribe((res:any) => {
      this.profile.getCheckIn(this.email,this.token)
      .subscribe((res:any) => { console.log(res.hangouts)
        this.loader = false;
        this.checkInData = res.hangouts;
      },err => { console.log(err)});
    },err => { this.loader = false; alert(err); console.log(err);
    })
  }

  addEdu(selectedData:any){
    //console.log(selectedData)
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.schoolElement.nativeElement,{types:['establishment']});
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          } console.log(place)
          this.eLat = place.geometry.location.lat(); 
          this.eLng = place.geometry.location.lng();
          this.eAddress = place.formatted_address;
          this.ePlaceName = place.name;
          this.ePlaceId = place.place_id;
        });
      });
    });
  }

  delEdu(id){
    this.profile.delEdu(this.token,this.email,id)
    .subscribe((res:any)=>{
      console.log(res)
      this.profile.getProfile(this.email,this.token)
      .subscribe((res:any)=>{
        localStorage.setItem('resData',JSON.stringify(res));
        this.setView(res);
      },(err)=>{console.log(err)});
    },(err)=>{console.log(err)});
  }

  setHometown(selectedData:any){
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement,{types:['address']});
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          } console.log(place)
          this.hLat = place.geometry.location.lat(); this.hLng = place.geometry.location.lng();
        });
      });
    });
  }

  setCity(selectedData:any) {
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.cityElement.nativeElement,{types:['address']});
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          } console.log(place)
          this.cLat = place.geometry.location.lat(); this.cLng = place.geometry.location.lng();
        });
      });
    });
  }

  viewProfile(userEmail){
    localStorage.setItem('frndEmail',userEmail);
    this.router.navigate(['/profile']);
  }

  viewMutualFriends(userEmail){
    this.mutualFriends = [];
    this.mfModal.show();
    this.loader = true;
    this.profile.getMutualFriends(this.email,this.token,userEmail)
    .subscribe((res:any)=>{
      this.loader = false;
      console.log(res); this.mutualFriends = res.mutual_friends;
    },err=>{ this.loader = false; console.log(err)});
  }

  editProfile(){
    this.edit = false;
    let birthday;
    birthday = this.dob !== undefined ? this.dob.nativeElement.value : null;
    if(birthday){
      const data = {dob : birthday+" 00:00:00"};
      this.profile.updateProfile(this.email,this.token,data)
      .subscribe((res:any)=>{
        localStorage.setItem('resData',JSON.stringify(res));
        this.age = this.getAge(res.dob.substr(0,res.dob.indexOf('T')));
      },(err)=>{
        alert('Birthday not Updated');
        console.log(err)
      });
    }
  }

  saveUpdate(updateType){
    if(updateType=='hometown'){
      console.log(this.hLat,this.hLng)
      if(this.hLat && this.hLng){
        const data = {city_lat : this.hLat, city_lng : this.hLng, city_source : 'GOOGLE_PLACES'};
        this.profile.updateProfile(this.email,this.token,data)
        .subscribe((res:any)=>{
          localStorage.setItem('resData',JSON.stringify(res));
          this.hometown = res.city.name +', '+ res.city.state;
          this.hideUpdateModal(); 
        },(err)=>{
          alert('Hometown not Updated');
          console.log(err)
        });
      }
    }else if(updateType=='city'){
      console.log(this.cLat,this.cLng)
      if(this.cLat && this.cLng){
        const data = {current_city_lat : this.cLat, current_city_lng : this.cLng, current_city_source : 'GOOGLE_PLACES'};
        this.profile.updateProfile(this.email,this.token,data)
        .subscribe((res:any)=>{
          localStorage.setItem('resData',JSON.stringify(res));
          this.city = res.current_city.name;
          this.state = res.current_city.state;
          this.hideUpdateModal(); 
        },(err)=>{
          alert('City not Updated');
          console.log(err)
        });
      }
    }else if(updateType=='org'){
      let organizationId;
      organizationId = this.orgId !== undefined ? this.orgId.nativeElement.value : null;
      if(organizationId){
        const data = {go_id:organizationId}
        this.profile.addOrg(this.token,this.email,data)
        .subscribe((res:any)=>{
          console.log(res)
          this.profile.getProfile(this.email,this.token)
          .subscribe((res:any)=>{
            localStorage.setItem('resData',JSON.stringify(res));
            this.setView(res);
            this.hideUpdateModal(); 
          },(err)=>{console.log(err)});
        },(err)=>{console.log(err)});
      }
    }else if(updateType=='edu'){
      //if(this.education.length > 0){
      let type = this.eduType.nativeElement.value;
      let raw_data = {address: this.eAddress, id: this.ePlaceId, lat: this.eLat, lng: this.eLng, name: this.ePlaceName};
      let data = [{title:this.ePlaceName,lat:this.eLat,lng:this.eLng,type:type,raw_data:raw_data,id:this.ePlaceId,source:'GOOLGE_PLACES',year:'2013-02-02 00:00:00'}]
      console.log(data)
        this.profile.addEdu(this.token,this.email,JSON.stringify(data))
        .subscribe((res:any)=>{
          console.log(res);
          this.profile.getProfile(this.email,this.token)
          .subscribe((res:any)=>{
            localStorage.setItem('resData',JSON.stringify(res));
            this.setView(res);
            this.education = [];
            this.hideUpdateModal(); 
          },(err)=>{console.log(err)});
        },(err)=>{console.log(err)});
      //}
    }
  }
  
  fbSync(){

  }
  
  fileChangeListener(event) {
    this.editDp = false;
    this.cropperSettings.width = event.target.dataset.width;
    this.cropperSettings.height = event.target.dataset.height;
    this.cropperSettings.compressRatio = this.cropperSettings.width / this.cropperSettings.height;
    this.cropperSettings.minWidth = event.target.dataset.width;
    this.cropperSettings.minHeight = event.target.dataset.height;
    this.imgType = event.target.name;
    var image:any = new Image();
    var file:File = event.target.files[0];
    var myReader:FileReader = new FileReader();
    var that = this;
    myReader.onloadend = function (loadEvent:any) {
        image.src = loadEvent.target.result;
        that.cropper.setImage(image);
    };
    myReader.readAsDataURL(file);
    this.editDp = false;
    this.showChildModal();
    this.updateModal.hide();
  }

  uploadTrigger(file:any){
    file.click();
  }

  uploadDpImg(img) {
    this.loader = true;
    let fileData;
    if(this.fileInput.nativeElement.files[0]){
      fileData = this.fileInput.nativeElement.files[0];
    }else{
      fileData = this.fileInput2.nativeElement.files[0];
    }
    let fname = fileData.name, mime = fileData.mime;
    let croppedImg = this.dataURLtoFile(img,fname,mime);
    this.imgUploading = 'Uploading Image..............';
    let formData:FormData = new FormData();
    formData.append('file', croppedImg);
    this.profile.uploadProfileImage(this.email,formData,this.imgType,this.token)
    .subscribe((res:any)=>{
      this.loader = false;
      this.imgUploading = '';
      localStorage.setItem('resData',JSON.stringify(res));
      this.image = this.imgBaseUrl + res.profile_image;
      if(this.editFImg == true) {this.childModal.hide();}else{this.hideChildModal();}
      if(res.featured_image){
        this.fImage = this.imgBaseUrl + res.featured_image[res.featured_image.length - 1];
        this.featuredImages = res.featured_image;
        if(this.imgType=='FEATURED_IMAGE') this.updateModal.show();
      }
    },(err)=>{
      this.loader = false;
      this.imgUploading = 'No image uploaded!';
      console.log(err);
    });
  }

  dataURLtoFile (dataurl, filename,mime) {
    let arr = dataurl.split(','),
        bstr = atob(arr[1]),
        n = bstr.length, 
        u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    if(mime === 'image/jpeg'){
      mime = 'image/jpg';
      filename = 'image.jpg';
    }
    return new File([u8arr], filename, {type: mime});
  }
  
  delPic(name){
    this.loader = true;
    if(!name){
      let img = JSON.parse(localStorage.getItem('resData')).profile_image;
      this.profile.deleteImage(this.email,img,this.token)
      .subscribe((res:any)=>{
        this.loader = false;
        this.profile.getProfile(this.email,this.token)
        .subscribe((res:any)=>{
          localStorage.setItem('resData',JSON.stringify(res));
          this.setView(res);
          this.editDp = false;
          this.image = '';
          this.score = res.score;
          this.hideUpdateModal(); 
        },(err)=>{console.log(err);this.loader = false;});
      },err=>{console.log(err);this.loader = false;});
    }else{
      this.profile.deleteImage(this.email,name,this.token)
      .subscribe((res:any)=>{
        this.loader = false;
        this.profile.getProfile(this.email,this.token)
        .subscribe((res:any)=>{
          localStorage.setItem('resData',JSON.stringify(res));
          this.setView(res);
          this.setImages(this.imgBaseUrl);
          this.score = res.score;
        },(err)=>{console.log(err);this.loader = false;});
      },err=>{console.log(err);this.loader = false;});
    }
  }

  setImages(baseUrl){
    this.fImage = '';
    let fimg,len;
    let img = JSON.parse(localStorage.getItem('resData')).profile_image;
    if(JSON.parse(localStorage.getItem('resData')).featured_image){
      len = JSON.parse(localStorage.getItem('resData')).featured_image.length;
      fimg = JSON.parse(localStorage.getItem('resData')).featured_image[len-1];
    }
    if(img !== undefined && baseUrl !== null){
      this.image = baseUrl + img;
    }else{
      this.image = '';
    }
    if(fimg){
      this.fImage = baseUrl + fimg;
    }
  }

  setView(data){
    this.email = data.email;
    this.featuredImages = data.featured_image;
    this.relationship = data.relationship;
    this.age1 = data.dob;
    if(this.age1) {let newAge = this.age1.substr(0,this.age1.indexOf('T'));this.age = this.getAge(newAge);}
    this.age1 = this.age1.substr(0,this.age1.indexOf('T'));
    this.name = data.name;
    this.gender = data.gender;
    this.score = data.score;
    if(Object.keys(data.current_city).length) {this.city = data.current_city.name;this.state = data.current_city.state;}
    if(Object.keys(data.city).length) {this.hometown = data.city.name +', '+ data.city.state;}
    if(Object.keys(data.gos).length) {this.serOrgData = data.gos;}else{this.nSerOrgData = 'Not Set';}
    if(Object.keys(data.education).length) {this.serEduData = data.education;}else{this.nSerEduData = 'Not Set';}
    this.sync = data.sync_status;this.shareLoc = data.location;this.eVerify = data.email_verified;
    this.pVerify = data.phone_number_verfied;
  }

  getAge(birthday):number{
    let dob = new Date(birthday); 
    var ageDifMs = Date.now() - dob.getTime();
    var ageDate = new Date(ageDifMs); // miliseconds from epoch
    let age = Math.abs(ageDate.getUTCFullYear() - 1970);
    return age;
  }

  enableEdit(btn){
    if(btn=='dp'){
      if(this.edit){
        this.editDp = true;
        this.updateModal.show();
      }
    }else if(btn=='fImg'){
      if(this.edit){
        this.editFImg = true;
        this.updateModal.show();
      }
    }else if(btn=='hometown'){
      this.editHomeTown = true;
      this.updateModal.show();
    }else if(btn=='city'){
      this.editCity = true;
      this.updateModal.show();
    }else if(btn=='org'){
      this.editOrg = true;
      this.updateModal.show();
    }else if(btn=='edu'){
      this.editEdu = true;
      this.updateModal.show();
    }
  }

  showChildModal(): void {
    this.childModal.show();
  }

  hideChildModal(): void {
    this.childModal.hide();
    if(this.editFImg == true || this.editDp == true) this.updateModal.show();
    this.editHomeTown = false; this.editCity = false; this.editOrg = false; this.editEdu = false;
    this.fileInput.nativeElement.value = "";
    this.fileInput2.nativeElement.value = "";
    this.imgUploading = "";
  }

  hideUpdateModal():void{ 
    if(this.editFImg){
      this.sortFeaturedImages(this.featuredImages)
    }
    if(this.cityElement) this.cityElement.nativeElement.value = ''; this.citySearch = new FormControl();
    if(this.searchElementRef) this.searchElementRef.nativeElement.value = ''; this.searchControl = new FormControl();
    if(this.schoolElement) this.schoolElement.nativeElement.value = ''; this.schoolSearch = new FormControl();
    this.editDp = false; this.editFImg = false;this.editHomeTown = false; this.editCity = false; this.editOrg = false; this.editEdu = false;
    this.updateModal.hide();
  }

  sortFeaturedImages(data){
    this.profile.sortFeatureImages(this.email,this.token,JSON.stringify(data))
    .subscribe((res:any)=>{
      localStorage.setItem('resData',JSON.stringify(res));
      this.setView(res);
      this.setImages(this.imgBaseUrl);
      alert('Sorted');
    },err=>{console.log(err)});
  }

  logout() {
  	localStorage.clear();
  	this.router.navigate(['login']);
  }
}



