import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'hangoutTime'
})
export class HangoutTimePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let start = value, end = args;
    let sAmPm = 'am',eAmPm ='am';
    let tindex = start.indexOf('T');
    let startTime = start.substr(tindex+1,start.length-1);
    let startTimeIndex = (start.substr(tindex+1,start.length-1)).indexOf('.');
        startTime = startTime.substr(0,startTimeIndex-2);
    let startHr = startTime.substr(0,2);
    if(startHr.includes(':')){ startHr = startTime.substr(0,1); }
    let startMin = startTime.substr(startHr.length+1,startHr.length+2); 
        tindex = end.indexOf('T');
    let endTime = end.substr(tindex+1,end.length-1);
    let endTimeIndex = (end.substr(tindex+1,end.length-1)).indexOf('.');
        endTime = endTime.substr(0,endTimeIndex-2);
    let endHr = endTime.substr(0,2);
    if(endHr.includes(':')){ endHr = endTime.substr(0,1); }
    let endMin = endTime.substr(endHr.length+1,endHr.length+2); 
    if(startHr > 12){ startHr = startHr-12; sAmPm = 'pm' }
    if(endHr > 12){ endHr = endHr-12; eAmPm = 'pm' }
    return startHr + sAmPm +'-' + endHr + eAmPm;
  }

}



