import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Constants } from "./constants";
import 'rxjs/add/operator/map';

@Injectable()
export class ProfileService {

  constructor(private http:HttpClient) { }

  config(){
    return this.http
          .get(Constants.API_URL+'config/fly')
          .map((res:any) => res);
  }

  uploadProfileImage(email,image,type,token){
    return this.http
          .post(Constants.API_URL+'profile/user/'+email+'/images/'+type,image,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  uploadHangoutImage(email,image,type,token){
    return this.http
          .post(Constants.API_URL+'storage/files/'+type,image,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res); 
  }

  uploadChatImage(token,image){
    return this.http
          .post(Constants.API_URL+'storage/files/CHAT_IMAGE',image,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res); 
  }

  hangoutInvitation(token,body){
    return this.http
          .post(Constants.API_URL+'profile/place/check_in/invite',body,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token) 
          })
          .map((res:any) => res);
  }

  getHangoutInvitedList(token,chatId,hangoutId){
    return this.http
          .get(Constants.API_URL+'profile/place/check_in/'+chatId+'/'+hangoutId+'/invited/page/0',{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token) 
          })
          .map((res:any) => res);
  }

  getAttendeeList(token,chatId,hangoutId){
    return this.http
          .get(Constants.API_URL+'profile/place/check_in/'+chatId+'/'+hangoutId+'/people/page/0',{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token) 
          })
          .map((res:any) => res);
  }

  addHangout(email,token,body){
    return this.http
          .post(Constants.API_URL+'profile/place/check_in',body,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res); 
  }

  updateHangout(email,token,body){
    return this.http
          .put(Constants.API_URL+'profile/place/check_in',body,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  getHangouts(email,token,type){
    return this.http
          .get(Constants.API_URL+'profile/place/check_in/'+email+'/page/0?hangout_type='+type,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  deleteImage(email,image,token){
    return this.http
          .delete(Constants.API_URL+'profile/user/'+email+'/images/'+image,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)  
          })
          .map((res:any)=> res);
  }

  shareLocation(email,token,data,action){
    return this.http
          .post(Constants.API_URL+'profile/user/'+email+'/fire_wall/LOCATION/'+action+'/ALL',data,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }
  
  chatHistory(token,myId,friendsId){
    let time = Date.now();
    return this.http
          .get(Constants.API_URL+'chat/history/'+myId+'/'+friendsId+'/'+time,{
            headers:new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  friendsList(email,token,degree,sortBy,page){
    return this.http
          .get(Constants.API_URL+'profile/user/'+email+ "/friends?degree="+degree+"&sort="+sortBy+"&page="+page,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  chatList(token,chat_id){
    return this.http
          .get(Constants.API_URL+'chat/chat_list/' + chat_id,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  favorite(token,email,friendsEmail){
    const data = {};
    return this.http
          .post(Constants.API_URL+'profile/user/' + email + "/fire_wall/TWO_DEGREES/FAVORITE/" + friendsEmail,data,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  getProfile(email,token){
    return this.http
          .get(Constants.API_URL+'profile/user/'+email,{
          headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);  
  }

  getbusinessProfiles(token,chat_id){
    return this.http
          .get(Constants.API_URL+'profile_business/user/'+chat_id,{
            headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);  
  }

  getMutualFriends(email,token,userEmail){
    return this.http
          .get(Constants.API_URL+'profile/user/'+email+'/mutual_friends/'+userEmail+'/page/0',{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token) 
          })
          .map((res:any)=> res);
  }

  updateProfile(email,token,data){
    return this.http
          .put(Constants.API_URL+'profile/user/'+email,data ,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  searchOrg(token,cat,searchStr){
    return this.http
          .get(Constants.API_URL+'go/search/'+cat+'/'+searchStr,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token) 
          })
          .map((res:any) => res);
  }

  addOrg(token,email,orgId){
    return this.http
          .post(Constants.API_URL+'profile/user/'+email+'/go',orgId,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any)=> res);
  }

  addEdu(token,email,data){
    const body = {education : data}
    return this.http
          .put(Constants.API_URL+'profile/user/'+email, body,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any)=> res);
  }

  delOrg(token,email,orgId){
    return this.http
          .delete(Constants.API_URL+'profile/user/'+email+'/go/'+orgId,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)   
          })
          .map((res:any)=> res);
  }

  delEdu(token,email,eduId){
    return this.http
          .delete(Constants.API_URL+'profile/user/'+email+'/education/'+eduId,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)   
          })
          .map((res:any)=> res);
  }

  sortFeatureImages(email,token,order){
    const body = {order: order};
    return this.http
          .put(Constants.API_URL+'profile/user/'+email+'/images/FEATURED_IMAGE',body,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)   
          })
          .map((res:any)=> res);
  }

  sendCode(email,type){
    let data = {};
    return this.http
          .post(Constants.API_URL+'profile/user/'+email+'/'+type+'/send', data, {
            headers: new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
            })
          .map((res:any) => res);
  }

  verify(email,type,pin){
    let formData:FormData = new FormData();
    formData.append('pin', pin);
    return this.http
          .post(Constants.API_URL+'profile/user/'+email+'/'+type+'/verify', formData, {
            headers: new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
            })
          .map((res:any)=> res);
  }

  blockedUsers(email,token){
    return this.http
          .get(Constants.API_URL+'profile/user/'+email+ "/fire_wall/TWO_DEGREES/BLOCK",{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  getCheckIn(email,token){
    return this.http
          .get(Constants.API_URL+'profile/place/check_in/'+email+'/page/0?hangout_type=CHECK_IN',{
            headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  delCheckIn(email,token,id){
    return this.http
          .delete(Constants.API_URL+'profile/place/check_in/'+email+'/'+id,{
            headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
            .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  countries(){
    return this.http
          .get(Constants.API_URL+'config/countries',{
            headers : new HttpHeaders()
            .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID) 
          })
          .map((res:any)=> res);
  }

}



