import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";
import { Md5 } from 'ts-md5/dist/md5';

declare var window: any;
declare var FB: any;

@Component({
  selector: 'app-fb-sync',
  templateUrl: './fb-sync.component.html',
  styleUrls: ['./fb-sync.component.css']
})
export class FbSyncComponent implements OnInit {

  constructor(private router:Router,private http: HttpClient) {
    (function(d, s, id){
			var js, fjs = d.getElementsByTagName(s)[0];
			if (d.getElementById(id)) {return;}
			js = d.createElement(s); js.id = id;
			js.src = '//connect.facebook.net/en_US/sdk.js';
			fjs.parentNode.insertBefore(js, fjs);
		}(document, 'script', 'facebook-jssdk'));
		window.fbAsyncInit = () => {
			console.log("fbasyncinit")
			FB.init({
				appId            : '620810454655743',
				autoLogAppEvents : true,
				status			 : true,
				cookie			 : true,
				xfbml            : true,
				version          : 'v2.6'
			});
			FB.AppEvents.logPageView();
		};
  }

  ngOnInit() {}

  syncfb(event){
    event.preventDefault();
    FB.getLoginStatus((response) => {
			if (response.status === 'connected') {
        let accessToken : string = response.authResponse.accessToken,
            user_id : string = response.authResponse.userID;
				this.GetFbData(accessToken,user_id);
			}else{
				this.initFBLogin(event);
			}
		})
  }

  initFBLogin(event){
		FB.login((response)=>{
			this.syncfb(event);
		},{scope:'email,user_friends,public_profile,read_friendlists'});
  }
  
  GetFbData(accessToken, userid){
    let access_token = localStorage.getItem('token'),
       objectData = localStorage.getItem('resData');
       if(!objectData || objectData == undefined){
        objectData = localStorage.getItem('surData');
       }
    let convrtedData = JSON.parse(objectData),
       chat_id = convrtedData.chat_id,
       uemail = convrtedData.email;
    //fetch data from twodegree friends from facebook
    FB.api('/me/friends', (response) => {console.log(response)
      let multidata = response.data;
      //get sync devices
      this.http.get(Constants.API_URL+'address_book/user/'+chat_id+'/device/ALL/syncs',{
        headers : new HttpHeaders()
        .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
        .set('X-TWO_DEGREE-APP_TOKEN',access_token)	
      })
      .subscribe((res:any) => {console.log(res);
        let devices_obj = res.devices,
            device_id = Object.keys(devices_obj);
            if (device_id[0]) {
              let get_td_arry = '';
              //get devices contacts data
              this.http.get(Constants.API_URL + 'address_book/v2/contact_card/' + chat_id + '/device/' + device_id[0] + "/page/0/limit/20",{
                headers : new HttpHeaders()
                .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
                .set('X-TWO_DEGREE-APP_TOKEN',access_token)	
              })
              .subscribe((res:any) => {console.log(res);
                let td_id_arry = [],
                    contacts_obj = res.contacts;
                for (let temp2 = 0; temp2 < Object.keys(contacts_obj).length; temp2++) {
                  if (contacts_obj[temp2].type == 'FACE_BOOK') {
                      td_id_arry.push(contacts_obj[temp2].td_id);
                  }
                }
                let raw_data = JSON.stringify(td_id_arry);
                if (raw_data) {
                    get_td_arry = JSON.parse(raw_data);
                }
                let i = 0,TD,fbUname,fbUid,Namearry,Fname,Lname,
                    dataenc : any = [];
                if (multidata.length > 0) {
                    let delete_td_id_array : any = [],
                        new_fb_td_id_list = [];
                    for (i = 0; i < multidata.length; i++) {
                          fbUid = multidata[i].id;
                          fbUname = multidata[i].name;
                          Namearry = fbUname.split(" ");
                          Fname = Namearry[0];
                          Lname = Namearry[1];
                          TD = fbUid + chat_id + Fname + Lname;
                          TD = Md5.hashStr(TD);
                      new_fb_td_id_list.push(TD);
                      if (get_td_arry.indexOf(TD) == -1) {
                          dataenc.push({"emails": [], "fb_id": fbUid, "phone_numbers": [], "last_name": Lname, "td_id": TD, "type": "FACE_BOOK", "first_name": Fname});
                      }
                    }
                    if (get_td_arry) {
                      for (let temp5 = 0; temp5 < get_td_arry.length; temp5++) {
                          if (new_fb_td_id_list.indexOf(get_td_arry[temp5]) == -1) {
                              delete_td_id_array.push(get_td_arry[temp5]);
                          }
                      }
                    }
                    dataenc = JSON.stringify(dataenc);
                    delete_td_id_array = JSON.stringify(delete_td_id_array);
                    let ourIp : any = '192.168.1.1';
                    var body = {'user': chat_id, 'added_contacts': dataenc, 'device_id': 'WEB_' + ourIp, 'account_id': userid, 'account_type': 'FACE_BOOK', 'delete_existing': 'FALSE', 'deleted_contacts': delete_td_id_array};
                    this.http.post(Constants.API_URL + 'address_book/v2/contact_card_update', body,{
                      headers:new HttpHeaders()
                      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
                      .set('X-TWO_DEGREE-APP_TOKEN',access_token)	
                    })
                    .subscribe((res:any) => {
                      console.log(res);
                      this.outFromFb();
                      localStorage.setItem('fbConSynMsg','Synced');
                      this.router.navigate(['/dashboard']);
                    },(err) => {
                      console.log(err);
                      this.outFromFb();
                      localStorage.setItem('fbConSynMsg','Not Synced');
                      this.router.navigate(['/dashboard']);
                    });
                }else{
                  let ourIp : any = '192.168.1.1';
                  let body = {'user': chat_id, 'added_contacts': '[{}]', 'device_id': 'WEB_' + ourIp, 'account_id': userid, 'account_type': 'FACE_BOOK', 'delete_existing': 'TRUE', 'deleted_contacts': '[]'};
                  this.http.post(Constants.API_URL + 'address_book/v2/contact_card_update', body,{
                    headers:new HttpHeaders()
                    .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
                    .set('X-TWO_DEGREE-APP_TOKEN',access_token)	
                  })
                  .subscribe((res:any) => {
                    console.log(res);
                    this.outFromFb();
                    localStorage.setItem('fbConSynMsg','Synced');
                    this.router.navigate(['/dashboard']);
                  },(err) => {
                    console.log(err);
                    this.outFromFb();
                    localStorage.setItem('fbConSynMsg','Not Synced');
                    this.router.navigate(['/dashboard']);
                  });
                }
              },(err) => {console.log(err);})
            }else{
              if (multidata.length > 0) {
                let i,TD,
                    dataenc : any = [];
                for (i = 0; i < multidata.length; i++) {
                    let fbUid = multidata[i].id,
                    fbUname = multidata[i].name,
                    Namearry = fbUname.split(" "),
                    Fname = Namearry[0],
                    Lname = Namearry[1];
                    TD = fbUid + chat_id + Fname + Lname,
                    TD = Md5.hashStr(TD);
                    dataenc.push({"emails": [], "fb_id": fbUid, "phone_numbers": [], "last_name": Lname, "td_id": TD, "type": "FACE_BOOK", "first_name": Fname});
                }
                dataenc = JSON.stringify(dataenc);
                let ourIp = '192.168.1.1';
                let body = {'user': chat_id, 'added_contacts': dataenc, 'device_id': 'WEB_' + ourIp, 'account_id': userid, 'account_type': 'FACE_BOOK', 'delete_existing': 'FALSE', 'deleted_contacts': '[]'};
                this.http.post(Constants.API_URL + 'address_book/v2/contact_card', body,{
                  headers : new HttpHeaders()
                  .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
                  .set('X-TWO_DEGREE-APP_TOKEN',access_token)	
                })
                .subscribe((res:any) => {
                  console.log(res);
                  this.outFromFb();
                  localStorage.setItem('fbConSynMsg','Synced');
                  this.router.navigate(['/dashboard']);
                },(err) => {
                  console.log(err);
                  this.outFromFb();
                  localStorage.setItem('fbConSynMsg','Not Synced');
                  this.router.navigate(['/dashboard']);
                });
              }else{
                this.router.navigate(['/dashboard']);
              }
            }
      },(err) => {console.log(err);});
    });
  }

  outFromFb(){
		FB.getLoginStatus((response:any) => {
			if (response && response.status === 'connected') {
				FB.logout((response:any) => {

				});
			}
		});
  }
  
  nothanks(event){
    event.preventDefault();
    this.router.navigate(['/dashboard']);
  }

}



