import { Component, OnInit,ViewChild,ElementRef,NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { DiscoverService } from '../discover.service';
import { GoogleMapsAPIWrapper } from '@agm/core/services/google-maps-api-wrapper';
import { } from 'googlemaps';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { MapsAPILoader,AgmMap } from '@agm/core';
import { ProfileService } from '../profile.service';

@Component({
  selector: 'app-discover',
  templateUrl: './discover.component.html',
  styleUrls: ['./discover.component.css']
})
export class DiscoverComponent implements OnInit {
  @ViewChild('checkInModal') checkInModal: ModalDirective;
  @ViewChild('checkIn') checkIn: ElementRef;
  @ViewChild(AgmMap) public agmMap: AgmMap;
  private email; private token; private sortBy; private type = "ONE_DEGREE"; query : string = '';
  private ciLat : number; private ciLng : number;
  friends : Array<any>;
  lat : number = 0; lng : number = 0; miles : number = 5; zoom : number = 2;
  loader : boolean = false;
  imageBaseUrl : string = localStorage.getItem('imgBaseUrl');
  dir = undefined; checkInRawData : any;

  constructor(private router:Router,private discover:DiscoverService,private profile:ProfileService,
    private _mapLoader:MapsAPILoader,private _mapsWrapper:GoogleMapsAPIWrapper,private zone:NgZone) {
    this.token = localStorage.getItem('token');
    if(!this.token || this.token === ''){
      router.navigate(['login']);
    }else{
      let userData = JSON.parse(localStorage.getItem('resData'));
      this.email = userData.email; this.sortBy = 'distance__ASC';
      this.lat = userData.lat; this.lng = userData.lng;
      this.loader = true;
      this.discover.friendsList('logics.tester@gmail.com',this.token,this.type,this.sortBy,0)
      .subscribe((res:any)=>{
        this.loader = false;
        this.friends = (res.friends);
        this.setZoom(this.friends);
      },err=>{console.log(err)
        this.loader = false;
        if(err.error.error === "1500"){
          localStorage.clear();
          this.router.navigate(['login']);
        }else if(!(err.ok)){
          alert('No connection.Reload page!');
        }
      });
    }
  }

  ngOnInit() {}

  setCheckIn(event){
    this._mapLoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.checkIn.nativeElement,{types:['establishment']});
      autocomplete.addListener('place_changed', () => {
        this.zone.run(() => {
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          } console.log(place)
          this.checkInRawData = place;
          this.ciLat = place.geometry.location.lat(); this.ciLng = place.geometry.location.lng();
        })
      })
    });
  }

  saveCheckIn(){
    this.loader = true;
    console.log(this.ciLat,this.ciLng);
    console.log(this.checkInRawData)
    let title = this.checkInRawData.name;
    let venue_id = this.checkInRawData.id;
    const data = {"user": this.email, "source": 'GOOGLE_PLACES', "title": title, "lat": this.ciLat, "lng": this.ciLng, "venue_id": venue_id, "raw_data": JSON.stringify(this.checkInRawData)};
    this.discover.checkIn(this.email,this.token,data)
    .subscribe((res:any) => {
      this.loader = false;
      alert('CheckIn Saved!');
      this.checkIn.nativeElement.value = '';
      this.checkInModal.hide();
      const data = {lat : this.ciLat,lng : this.ciLng}
      this.profile.updateProfile(this.email,this.token,data)
      .subscribe((res:any) => {
        localStorage.setItem('resData',JSON.stringify(res));
      },err => { console.log('Profile Not Fetched')});
    },err => { this.loader = false; alert('CheckIn Fails'); console.log(err)})
  }

  search(event){
    let search = event.target.value;
    this.loader = true;
    if(search.length > 2){
      this.discover.searchFilter(this.email,this.token,search)
      .subscribe((res:any)=>{
        this.loader = false;
        if(res.friends.length > 0){
          this.friends = [];
          this.friends = res.friends;
          this.setZoom(this.friends);
        }else{
          alert('No friends Found');
          event.target.value = '';
        }
      },err=>{this.loader = false;console.log(err)});
    }else{
      this.discover.friendsList('logics.tester@gmail.com',this.token,'ZERO_DEGREE','distance_ASC',0)
      .subscribe((res:any)=>{
        this.loader = false;
        this.friends = (res.friends);
        this.setZoom(this.friends);
      },err=>{this.loader = false;console.log(err)});
    }
  }

  friendType(event){
    this.friends = [];
    this.loader = true;
    this.sortBy = 'distance__ASC';
    this.type = event.target.value;
    this.discover.friendsList(this.email,this.token,this.type,'distance__ASC',0)
    .subscribe((res:any)=>{
      this.loader = false;
      this.friends = (res.friends);
      this.setZoom(this.friends);
    },err=>{this.loader = false;console.log(err)});
  }

  sortByFilter(event){
    if(event.target.value){
      this.friends = [];
      this.loader = true;
      this.sortBy = event.target.value;
      this.discover.friendsList(this.email,this.token,'ZERO_DEGREE',this.sortBy,0)
      .subscribe((res:any)=>{
        this.loader = false;
        this.friends = (res.friends);
        this.setZoom(this.friends);
        this.dir = undefined;
      },err=>{this.loader = false;console.log(err)});
    }
  }

  getdistance(lat1, lon1, lat2, lon2) {
    var unit = "K";
    var radlat1 = Math.PI * lat1 / 180
    var radlat2 = Math.PI * lat2 / 180
    var theta = lon1 - lon2
    var radtheta = Math.PI * theta / 180
    var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
    dist = Math.acos(dist)
    dist = dist * 180 / Math.PI
    dist = dist * 60 * 1.1515
    if (unit == "K") {
        dist = dist * 1.609344
    }
    var num = dist * 0.621371;
    if(Number(num.toFixed(2)) < 0.18){
      var distance_with_feet =  Number(num.toFixed(2)) * 5280;
      var distance_with_feet = Math.round(distance_with_feet * 10) / 10;
      return distance_with_feet+' ft';
    }else{
      return Number(num.toFixed(2))+' mile';
    }
  }

  milesFilter(event){
    this.loader = true;
    this.friends = [];
    this.miles = event.target.value;
    let km = this.miles * 1.60934;
    this.discover.distanceFilter(this.email,this.token,'ZERO_DEGREE',km,'distance__ASC',0)
    .subscribe((res:any)=>{
      this.loader = false;
      this.friends = res.friends;
      this.setZoom(this.friends);
      this.dir = false;
    },err=>{this.loader = false; console.log(err)});
  }
  
  setZoom(data){
    let distances = []
    for(let i=0;i<data.length;i++){
      let distance = this.getdistance(this.lat,this.lng,data[i].lat,data[i].lng);
      distances.push(Number(distance.split(" ")[0]))
      let max:number = Math.max.apply( Math, distances);
      if(max > 100){
        this.zoom = 2;
      }else{
        this.zoom = 12;
      }
    }
  }

  echo(lat,lng){
    let distance;
    if(lat == 0 && lat == 0){
      this.zoom = 2;
      this.dir = undefined;
    }else{
      distance = this.getdistance(this.lat,this.lng,lat,lng);
    let newDist:number = Number(distance.split(" ")[0]);
      if(newDist > 100){
        this.zoom = 2;
        this.dir = {
          origin: { lat: this.lat, lng: this.lng },
          destination: { lat: lat, lng: lng }
        }
        console.log(this.dir)
        console.log(this.agmMap)
      }else{
        this.zoom = 12;
        this.dir = {
          origin: { lat: this.lat, lng: this.lng },
          destination: { lat: lat, lng: lng }
        }
      }
    }
  }

  reset(){
    this.checkIn.nativeElement.value = '';
  }

}



