import { Component, OnInit, OnChanges, SimpleChange, NgZone } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";
//import { timeout } from 'q';
//import { HttpBackend } from '@angular/common/http/src/backend';
declare var window: any;
declare var FB: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	logError = ''; loader : boolean = false;
	loginForm : FormGroup;

  constructor(private router:Router, private http: HttpClient,fb : FormBuilder,private zone : NgZone) {
		this.loginForm = fb.group({
			'email' : ['', Validators.compose([Validators.required, Validators.email])],
			'password' : ['', Validators.compose([Validators.required])]
		});
		(function(d, s, id){
			var js, fjs = d.getElementsByTagName(s)[0];
			if (d.getElementById(id)) {return;}
			js = d.createElement(s); js.id = id;
			js.src = '//connect.facebook.net/en_US/sdk.js';
			fjs.parentNode.insertBefore(js, fjs);
		}(document, 'script', 'facebook-jssdk'));
		window.fbAsyncInit = () => {
			console.log("fbasyncinit")
			FB.init({
				appId            : '620810454655743',
				autoLogAppEvents : true,
				status			 : true,
				cookie			 : true,
				xfbml            : true,
				version          : 'v2.6'
			});
			FB.AppEvents.logPageView();
		};
	}

  ngOnInit() {
		const token = localStorage.getItem('token');
		if(token){
			this.router.navigate(['dashboard']);
		}
  }

  onClickSubmit(data) {
  	const body = {account_id : data.email ,password : data.password ,login_type: 'EMAIL'};
	this.loader = true;
	this.http
	  .post(Constants.API_URL+'profile/login', body, {
    	headers: new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID),
  		})
	  .subscribe((res:any) => {
		this.loader = false;
	  	localStorage.setItem('token',res.access_token);
		localStorage.setItem('chat_id',res.chat_id);
		localStorage.setItem('resData',JSON.stringify(res));  
	  	this.router.navigate(['/dashboard']);
	  },err => {
		this.loader = false;
		console.log(err);
		this.logError = (err.error.message);
		if(err.error.error === "2003"){
			localStorage.setItem('email', JSON.stringify(data.email));
			setTimeout(() => { this.logError = ''; this.router.navigate(['/verifyPhone']); }, 2000);
		}
	  });
	}
	
	checkFacebookLogin(event){
		event.preventDefault();
		FB.getLoginStatus((response) => {
			if (response.status === 'connected') {
				let accessToken : string = response.authResponse.accessToken;
				this.FbLoginAPI(accessToken);
			}else{
				this.initFBLogin(event);
			}
		})
	}

	initFBLogin(event){
		FB.login((response)=>{
			this.checkFacebookLogin(event);
		},{scope:'email,public_profile'});
	}

	FbLoginAPI(token){
		this.loader = true;
		let url : string = '/me?fields=name,email,about,gender';
		FB.api(url, (response) => {
			let gender : string = response.gender,
				email : string = response.email,
				user_id : string = response.id;
			this.http.get(Constants.API_URL+'profile/check_social_login/FACE_BOOK/'+user_id,{
				headers:new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)	
			})
			.subscribe((res:any) => {
				const body = {account_id: user_id, password : token, login_type : 'FACE_BOOK'};
				this.http
				.post(Constants.API_URL+'profile/login', body, {
				  headers: new HttpHeaders().set('X-TWO_DEGREE-APP_ID', Constants.APP_ID),
					})
				.subscribe((res:any) => {
					this.loader = false;
					this.outFromFb();
					console.log(res);
					localStorage.setItem("resData", JSON.stringify(res));
					localStorage.setItem("token", res.access_token);
					localStorage.setItem('chat_id',res.chat_id);
					this.zone.run(() => this.router.navigate(['/dashboard']));
				},(err) => {
					this.loader = false;
					this.outFromFb();
					console.log(err)
					this.logError = err.error.message
				})
			},(err) => {
				this.loader = false;
				this.outFromFb();
				console.log(err);
				if(err.error.error === '2001'){
					this.logError = err.error.message;
				}
			})
		})
	}

	outFromFb(){
		FB.getLoginStatus((response:any) => {
			if (response && response.status === 'connected') {
				FB.logout((response:any) => {

				});
			}
		});
	}

}



