import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-fb-signup',
  templateUrl: './fb-signup.component.html',
  styleUrls: ['./fb-signup.component.css']
})
export class FbSignupComponent implements OnInit {
  fbSignupForm : FormGroup;

  constructor(private router:Router,fb : FormBuilder) {
    let data = JSON.parse(localStorage.getItem('sufData'));
    this.fbSignupForm = fb.group({
      'name' : [{value : data.name, disabled : true}],
      'gender' : [{value : data.gender, disabled : true}]
    })
  }

  ngOnInit() {
    setTimeout(() => { this.router.navigate(['/fbSync']); }, 5000);
  }

  next(event){
    this.router.navigate(['/fbSync']);
  }

}



