import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Constants } from "./constants";
import 'rxjs/add/operator/map';

@Injectable()
export class DiscoverService {

  constructor(private http:HttpClient) { }

  friendsList(email,token,degree,sortBy,page){
    let newemail = email != 'logics.tester@gmail.com' ? email : 'logics.tester@gmail.com'; 
    return this.http
          .get(Constants.API_URL+'profile/user/'+newemail+ "/friends?degree="+degree+"&sort="+sortBy+"&page="+page,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  distanceFilter(email,token,degree,distance,sortBy,page){
    let newemail = email != 'logics.tester@gmail.com' ? email : 'logics.tester@gmail.com'; 
    return this.http
          .get(Constants.API_URL+'profile/user/'+'logics.tester@gmail.com'+ "/friends?degree="+degree+"&distance="+distance+"&sort="+sortBy+"&page="+page,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  searchFilter(email,token,user_name){
    return this.http
          .get(Constants.API_URL+'profile/user/'+'logics.tester@gmail.com'+ "/friends?degree=ZERO_DEGREE&sort=distance__ASC&page=0&user_name="+user_name,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }

  checkIn(email,token,data){
    return this.http
          .post(Constants.API_URL+'profile/place/check_in',data,{
          headers : new HttpHeaders()
          .set('X-TWO_DEGREE-APP_ID',Constants.APP_ID)
          .set('X-TWO_DEGREE-APP_TOKEN',token)
          })
          .map((res:any) => res);
  }
}



