import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Constants } from "../constants";

@Component({
  selector: 'app-verify-email',
  templateUrl: './verify-email.component.html',
  styleUrls: ['./verify-email.component.css']
})
export class VerifyEmailComponent implements OnInit {
  email:string;
  emailVerifyForm : FormGroup;
  show : boolean = true;

  changeEmailErr : string = '';
  resendMsg : string = '';
  verifyErr : string = '';

  constructor(private http:HttpClient,fb : FormBuilder,private router:Router) {
    this.emailVerifyForm = fb.group({
      'pin' : [''],
      'email' : ['']
    })
  }

  ngOnInit() {
    this.email = JSON.parse(localStorage.getItem('newsurData')).new_email;
    if(this.email == '' || this.email == null || this.email === undefined)
      this.email = JSON.parse(localStorage.getItem('resData')).email;
  }

  verifyEmail(data,event){
    event.preventDefault();
    console.log(data.pin);
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    let formData:FormData = new FormData();
    formData.append('pin', data.pin);
    this.http
    .post(Constants.API_URL+'profile/user/'+email+'/'+'EMAIL/verify', formData, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        console.log(res);
        localStorage.setItem('resData',JSON.stringify(res));
        this.router.navigate(['/fbSync']);
      },(err) => {
        this.verifyErr = err.error.message;
        setTimeout(() => { this.verifyErr = ''; }, 2000);
        console.log(err);
      }
    );
  }

  updateEmail(data,event){
    event.preventDefault();
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    const senddata = {'email': data.email};
    this.http
    .put(Constants.API_URL+'profile/user/'+email+'/edit', senddata,{
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        localStorage.setItem('newsurData',JSON.stringify(res));
        this.email = res.new_email;
        this.show = true;
        //Send verification code
        this.resendECode(event);
        console.log(res);
      },(err) => {
        console.log(err);
        if(err.error.detail_description.email)
        this.changeEmailErr = err.error.detail_description.email[0];
        setTimeout(() => { this.changeEmailErr = ''; }, 2000);
      }
    );
  }

  resendECode(event){
    event.preventDefault();
    let email:string = JSON.parse(localStorage.getItem('surData')).email;
    let data = {};
    this.http
    .post(Constants.API_URL+'profile/user/'+email+'/Email/send', data, {
      headers: new HttpHeaders()
      .set('X-TWO_DEGREE-APP_ID', Constants.APP_ID)
      })
    .subscribe(
      (res:any) => {
        this.resendMsg = res.message;
        setTimeout(() => { this.resendMsg = ''; }, 2000);
        console.log(res);    
      },(err) => {
        alert(err.error.message);
        console.log(err);
      }
    )
  }
  changeEmail(event){
    event.preventDefault();
    this.show = false;
    this.emailVerifyForm.get('email').reset();
  }

  cancel($event){
    event.preventDefault();
    this.show = true;
    this.changeEmailErr = '';
  }

}



