import { Component, OnInit,ViewChild ,ElementRef ,NgZone } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { MapsAPILoader } from '@agm/core';
import { ImageCropperComponent, CropperSettings, Bounds} from 'ng2-img-cropper';
import { ProfileService } from '../profile.service';
import { Router } from '@angular/router';
import { DistancePipe } from '../distance.pipe';
import { HangoutTimePipe } from '../hangout-time.pipe';

@Component({
  selector: 'app-hangout',
  templateUrl: './hangout.component.html',
  styleUrls: ['./hangout.component.css']
})
export class HangoutComponent implements OnInit {
  @ViewChild("search") public searchElementRef: ElementRef;
  @ViewChild("hangoutModal") hoModal: ModalDirective;
  @ViewChild("cropperModal") cropperModal: ModalDirective;
  @ViewChild("mapModal") mapModal: ModalDirective;
  @ViewChild("friendsListModal") friendsListModal: ModalDirective;
  @ViewChild("hangoutName") hoName: ElementRef;
  @ViewChild('cropper') cropper:ImageCropperComponent;
  @ViewChild('hangoutImg') hangoutImg:any;
  @ViewChild('hoStart') hangoutStart:any;
  @ViewChild('hoEnd') hangoutEnd:any;
  @ViewChild('hoDescription') hangoutDes:any;
  @ViewChild('visibility') visibility:any;
  @ViewChild('guestList') guestList:any;
  @ViewChild('pbid') pbid:any;
  @ViewChild('sponsoredMsg') sponsoredMsg:any;
  allowAttendee:any;
  
  hoRawData: any; hoTitle: any; hoVenueId: any;
  private hoLat; private hoLng; private hangoutPLace; private hangoutName; private imgType; sponsored;
  private email; private token; private modalMapHoLat; private modalMapHoLng; target;
  hideHangoutBtn : boolean = false; addMoreDetails : boolean = false; userHangout : boolean = false; loader: boolean = false;
  cropperSettings: CropperSettings; data :any; showHoDetail : boolean = false;
  imgUploading : string = ''; image : any; imgBaseUrl : string = ''; lat : any; lng : any;
  serHangoutsDetail: Array<any>; hangoutsDetail: any; businessProfile: any;
  userData : any; friends : Array<any>;

  constructor(private mapsAPILoader: MapsAPILoader,private zone: NgZone,private profile:ProfileService,private router:Router) {
    this.token = localStorage.getItem('token');
  	if(!this.token){
  		router.navigate(['login']);
    }
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.noFileInput = true;
    this.cropperSettings.preserveSize = true;
    this.cropperSettings.keepAspect = false;
    this.cropperSettings.canvasWidth = 500;
    this.cropperSettings.canvasHeight = 400;
    this.data = {}; 
  }

  ngOnInit() {
    this.loader = true;
    let userData = JSON.parse(localStorage.getItem('resData'));
    this.imgBaseUrl = localStorage.getItem('imgBaseUrl');
    this.userData = userData;
    this.lat = userData.lat; this.lng = userData.lng;
    this.email = userData.email;
    this.image = localStorage.getItem('hangoutImage') != null ? localStorage.getItem('hangoutImage') : '';
    this.addMoreDetails = this.image ? true : false;
    //hangouts list
    this.profile.getHangouts(this.email,this.token,'ALL')
    .subscribe((res:any)=>{console.log(res)
      this.loader = false;
      this.serHangoutsDetail = res.hangouts;
    },err=>{this.loader = false;console.log(err)});
  }

  setHangoutLatLng(event){
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement,{types:['establishment']});
      autocomplete.addListener("place_changed", () => {
        this.zone.run(() => {
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          } console.log(place)
          this.hideHangoutBtn = true;
          this.hangoutPLace = place.name;
          this.hoLat = place.geometry.location.lat();
          this.hoLng = place.geometry.location.lng();
          this.hoRawData = JSON.stringify(place);
          this.hoTitle = place.name;
          this.hoVenueId = place.id;
        });
      });
    });
  }

  getInvitedList(hangoutId){
    //Invited Persons list
    this.profile.getHangoutInvitedList(this.token,this.userData.chat_id,hangoutId)
    .subscribe((res:any)=>{console.log(res)},err=>{console.log(err)});
  }

  getAttendeeList(hangoutId){
    //Persons attending the hangout
    this.profile.getAttendeeList(this.token,this.userData.chat_id,hangoutId)
    .subscribe((res:any)=>{console.log(res)},err=>{console.log(err)});
  }

  showDetails(id){
    this.showHoDetail = true;
    for(let i=0;i < Object.keys(this.serHangoutsDetail).length;i++){
      if(this.serHangoutsDetail[i].id == id){
        this.hangoutsDetail = this.serHangoutsDetail[i]; 
      }
    }
  }

  getBusinessProfile(){
    let chat_id = this.userData.chat_id;
    if(this.sponsored == true){
      this.profile.getbusinessProfiles(this.token,chat_id)
      .subscribe((res:any)=>{
        if(res.business_list.length < 1){
          alert('No Business Profile is Associated With this Account!');
          this.sponsored = false;
        }else{
          this.businessProfile = res.business_list;
        }
      });
    }
  }

  viewHangoutOnMap(lat,lng){
    this.modalMapHoLat = lat;
    this.modalMapHoLng = lng;
    this.mapModal.show();
  }

  inviteForHangout(friendsId){
    const body = {user_chat_id : this.userData.chat_id, hangout : this.hangoutsDetail.id, friend_chat_id : friendsId}
    this.profile.hangoutInvitation(this.token,body)
    .subscribe((res:any) => {
      alert('Invitation Sent to '+friendsId);
    },err => {console.log(err)});
  }

  showFriendsList(){
    this.friends = JSON.parse(localStorage.getItem('friends')).friends;
    console.log(this.friends)
    this.friendsListModal.show();
  }

  saveHangout(){
    let temp = this.hangoutStart.nativeElement.value.replace(/T/, ' ');
    let start = temp + ':00';
        temp = this.hangoutEnd.nativeElement.value.replace(/T/, ' ');
    let end = temp + ':00';
    let  temp2 = this.image.substr(this.image.lastIndexOf('/'),this.image.length-1)
    let imageData = temp2.replace(/\//,'');
    let image : Array<any> = [];
        image.push(imageData);
    let visibility = (this.visibility) ? this.visibility.nativeElement.value : '';
    let guest_list_type = (this.guestList) ? this.guestList.nativeElement.value : '';
    let description = (this.hangoutDes) ? this.hangoutDes.nativeElement.value : '';
    let inviteCat = this.allowAttendee ? 'ATTENDEE' : 'CLOSED';
    const body = {"user": this.email, "source": 'GOOGLE_PLACES', "title": this.hoTitle, "lat": this.hoLat,"lng": this.hoLng, "venue_id": this.hoVenueId, "raw_data": this.hoRawData, "name": this.hangoutName,"start": start, "end": end, "visibility": visibility, "featured_images":JSON.stringify(image), "invite_category":inviteCat,"guest_list_type": guest_list_type, "description":description};
    if(this.sponsored == true) {
      body['profile_business'] = this.pbid.nativeElement.value;
      body['radius'] = this.target;
      body['message_users'] = this.sponsoredMsg.nativeElement.value;
    }
    this.profile.addHangout(this.email,this.token,body)
    .subscribe((res:any)=>{
      this.addMoreDetails = false;
      localStorage.removeItem('hangoutImage');
      alert('Hangout Created');
      this.profile.getHangouts(this.email,this.token,'ALL')
      .subscribe((res:any)=>{console.log(res)
        this.loader = false;
        this.serHangoutsDetail = res.hangouts;
      },err=>{this.loader = false;console.log(err)});
    },err=>{ alert('Error!'); console.log(err)});
  }

  fileChangeListener(event) {
    this.cropperSettings.width = event.target.dataset.width;
    this.cropperSettings.height = event.target.dataset.height;
    this.cropperSettings.compressRatio = this.cropperSettings.width / this.cropperSettings.height;
    this.cropperSettings.minWidth = event.target.dataset.width;
    this.cropperSettings.minHeight = event.target.dataset.height;
    this.imgType = event.target.name;
    var image:any = new Image();
    var file:File = event.target.files[0];
    var myReader:FileReader = new FileReader();
    var that = this;
    myReader.onloadend = function (loadEvent:any) {
        image.src = loadEvent.target.result;
        that.cropper.setImage(image);
    };
    myReader.readAsDataURL(file);
    this.cropperModal.show();
  }

  uploadFImg(img){
    let fileData = this.hangoutImg.nativeElement.files[0];
    let fname = fileData.name, mime = fileData.type;
    console.log(fileData);
    let croppedImg = this.dataURLtoFile(img,fname,mime);
    console.log(croppedImg);
    this.imgUploading = 'Uploading Image..............';
    
    let formData:FormData = new FormData();
    formData.append('file', croppedImg);
    
    this.profile.uploadHangoutImage(this.email,formData,this.imgType,this.token)
    .subscribe((res:any)=>{
      this.imgUploading = '';
      localStorage.setItem('hangoutImage',res.full_path);
      this.image = res.full_path;
      this.cropperModal.hide();
    },(err)=>{
      this.imgUploading = 'No image uploaded!';
      console.log(err);
    });
  }

  dataURLtoFile (dataurl, filename,mime) {
    let arr = dataurl.split(','),
        bstr = atob(arr[1]),
        n = bstr.length, 
        u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    if(mime === 'image/jpeg'){
      mime = 'image/jpg';
      filename = 'image.jpg';
    }
    return new File([u8arr], filename, {type: mime});
  }

  editPlace(event){
    this.hoModal.show();
    this.hideHangoutBtn = false;
    this.addMoreDetails = false;
  }

  editHangout(id){
    for(let i=0;i < Object.keys(this.serHangoutsDetail).length;i++){
      if(this.serHangoutsDetail[i].id == id){ 
        localStorage.setItem('clickedHangout',JSON.stringify(this.serHangoutsDetail[i]));
      }
    }
    this.router.navigate(['/editHangout']);
  }

  addMoreDetailsToHangout(event){
    this.hangoutName = (this.hoName) ? this.hoName.nativeElement.value : 'Not Set'; 
    this.addMoreDetails = true;
    this.hoModal.hide();
  }

  reaction(res,id){
    const body = { user : this.email, hangout : id, action : res }
    this.profile.updateHangout(this.email,this.token,body)
    .subscribe((res:any) => {
      this.profile.getHangouts(this.email,this.token,'ALL')
      .subscribe((res:any)=>{console.log(res)
        this.loader = false;
        this.serHangoutsDetail = res.hangouts;
      },err=>{this.loader = false;console.log(err)});
    },err =>{
      alert('Failed!');
    });
  }

  hideCropperModal(){
    this.cropperModal.hide();
    this.hangoutImg.nativeElement.value = "";
  }
}



