import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'phone'
})
export class PhonePipe implements PipeTransform {

  transform(val: any, args?: any): any {
    let val1 = val.substr(0,3);
    let val2 = val.substr(3,3);
    let val3 = val.substr(6,6);
    let newStr = '('+val1+') '+val2+' '+val3;
    return newStr;
  }

}



